  constructor() {
    super();
    this.state = {
      timeRange: 14,
      country: '',
      coutryFlag: {},
      networks: {
        facebook: true,
        flurry: true,
        admob: true,
        marketPlace: true
      },
      platform: '',
      adType: '',
      chartType: 'adsAnalysis',
      StockData: [],
      chartData: [],
      selected: -1,
      done: [],
      predictValue: {
        admob: {
          fittedvalues: 0
        },
        fan: {
          fittedvalues: 0
        },
        flurry: {
          fittedvalues: 0
        },
        marketplace: {
          fittedvalues: 0
        }
      }
    }
  }

  componentDidMount() {
    // this.fetchCountryFlag();
    this.fetchAlertData();
  }

  fetchAlertData() {
    fetch(`https://kaishuang1004.github.io/stock_analysis/file.json`)
      .then(res => res.json())
      .then(data => {
        if (data.status === false && data.authStatus === false) {
          window.googleToken = window.gapi.auth2.getAuthInstance().currentUser.get().getAuthResponse().id_token;
          this.fetchAlertData();
          return;
        }
        this.setState({
          StockData: data
        });
      });
  }

